import PropTypes from 'prop-types';
import { useState } from 'react';
import { styled } from 'styled-components';

import IconButton from '#components/IconButton';

import TickSVG from '#icons/check-solid-16.svg';
import CopySVG from '#icons/copy-outline-16.svg';

const Wrapper = styled(IconButton)`
  &:disabled {
    color: ${({ theme }) => theme.color.semiotic.success} !important;
  }
`;

const CopyButton = ({ className, value, timeout = 1.5, isCosy = true }) => {
  const [isCopied, setIsCopied] = useState(false);

  async function copyTextToClipboard(text) {
    return await navigator.clipboard.writeText(text);
  }

  const handleCopyClick = async () => {
    try {
      await copyTextToClipboard(value);

      setIsCopied(true);

      setTimeout(() => {
        setIsCopied(false);
      }, timeout * 1000);
    } catch (err) {
      console.error(err);
    }
  };

  return (
    <Wrapper
      className={className}
      svg={isCopied ? TickSVG : CopySVG}
      size="small"
      onClick={handleCopyClick}
      isDisabled={isCopied}
      isCosy={isCosy}
    />
  );
};

CopyButton.propTypes = {
  className: PropTypes.string,
  value: PropTypes.string.isRequired,
  timeout: PropTypes.number,
  isCosy: PropTypes.bool,
};

export default CopyButton;
