import PropTypes from 'prop-types';
import { styled } from 'styled-components';

export const Table = ({ children, className, colCount = 1, colWidth = 10 }) => {
  const rowHeadingWidth = 100 - colCount * colWidth;
  const cols = [];

  for (let i = 0; i < colCount; i++) {
    cols.push(<col key={i} width={`${colWidth}%`} />);
  }

  return (
    <table className={className}>
      <colgroup>
        <col width={`${rowHeadingWidth}%`} />
        {cols}
      </colgroup>
      {children}
    </table>
  );
};

Table.propTypes = {
  children: PropTypes.oneOfType([
    PropTypes.node,
    PropTypes.arrayOf(PropTypes.node),
  ]).isRequired,
  className: PropTypes.string,
  colCount: PropTypes.number,
  colWidth: PropTypes.number,
};

export const Description = styled.small`
  color: ${({ theme }) => theme.color.ink.light};
  font: 12px/16px ${(props) => props.theme.fontFamily.secondaryFont};
`;

export const Group = styled.article`
  --cell-padding: ${({ theme }) => theme.spacing.x1}px
    ${({ theme }) => theme.spacing.x1_5}px;
  margin: ${({ theme }) => theme.spacing.x2}px 0;
`;

export const GroupHeader = styled.header`
  padding: var(--cell-padding);

  ${Description} {
    margin-left: ${({ theme }) => theme.spacing.x1}px;
  }
`;

export const GroupTitle = styled.h1`
  margin: 0;
  font: bold 14px/18px ${(props) => props.theme.fontFamily.secondaryFont};
`;

export const GroupTable = styled(Table)`
  width: 100%;
  border-collapse: collapse;
  table-layout: fixed;
`;

export const Headings = styled(GroupTable)`
  width: 100%;
  border-collapse: collapse;
  table-layout: fixed;

  th {
    padding: 0 ${({ theme }) => theme.spacing.x1_5}px;
    border: 1px solid transparent;
    font: 16px/20px ${({ theme }) => theme.fontFamily.secondaryFont};
  }
`;

export const Cell = styled.td`
  padding: var(--cell-padding);
  border: 1px solid ${({ theme }) => theme.color.divider.light};
  text-align: center;

  &:last-child {
    border-right: 0;
  }
`;

export const CellContents = styled.div`
  display: flex;
  align-items: center;
  justify-content: center;
  gap: ${({ theme }) => theme.spacing.x2}px;
`;

export const RowHeading = styled(Cell).attrs({ as: 'th' })`
  border-left: 0;
  font: normal 12px/16px monospace;
  text-align: left;
`;
